.footer {
    position: relative;
    width: 100%; /* Changed from 100vw */
    background-color: #0f7d6f;
    color: white;
    margin-top: auto;
    z-index: 10;
    left: 0; /* Ensure it's aligned to the left edge */
}

.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    padding: 0; /* Remove any padding */
    margin: 0; /* Remove any margin */
    position: relative;
    background-color: #B8E0DA;
    width: 100%;
    overflow-x: hidden; /* Prevent horizontal scrolling */
}

.box {
    width: 100%; /* Changed from 100vw */
    display: flex;
    flex-direction: column;
}
.box-one, .box-two, .box-three {
    width: 100%; /* Changed from 100vw */
    box-sizing: border-box; /* Include padding in width calculation */
}

.box-one {/*------------------------------------box-1--------------------------------------*/
    margin-top: 70px;
    display: flex;
    text-align: center;
    justify-content: center;
    align-content: center;

    background-color: #0f7d6f;
    margin-bottom: 30px;
}


.logo_slogan{
    display: flex;
    flex-direction: column;
    font-weight: 500;
}

.footer-logo{
    position: relative;
    width: 200px;
    left: 230px;
    margin-bottom: 20px;

}

.logo-footer p1{
    font-size: 15pt;
}

.logo-footer p2{
    font-size: 14pt;
    margin-bottom: 50px;
    margin-top: 20px;
}


.navbar-content{
    position: relative;

    display: flex;
    flex-direction: row;
    gap: 10px;
    left: 230px;
}

.navbar-content a
{
    color: white;
    font-weight: normal;
    text-decoration: none;
}


.navbar-content a:hover
{
    color: black;
    cursor: pointer;
}

.box-two{/*------------------------------------box-2--------------------------------------*/
    margin-top: 35px;
    display: flex;
    width: 100vw;
    flex-direction: column;
    text-align: left;
    padding-left: 100px;

    margin-bottom: 50px;
}

.address h1,
.contact h1,
.other h1{
    font-size: 16pt;
}

.address p{
    text-align: left;
}


.footer-links ul {
    display: flex;
    list-style: none;
    flex-direction: column;
    color: white;
    font-size: medium;
    text-decoration: none;
    justify-content: center;

}


hr { /*the line*/
    border: none;
    width: 100%;
    border-top: 2px solid #ccc;



}

.contact p{
    text-align: left;
}

.social-media{
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.instagram,
.facebook,
.youtube,
.twitter
{
    width: 30px;
}

.box-three{
    margin-top: 50px;
    margin-bottom: 80px;
}

.copyright{
    font-weight: 500;
}